body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fullpage {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* background-color: #daf7e5; */

}
.first{
   display: flex;
   flex-direction: row;
   justify-content: center;
   align-items: center;
   width: 12rem;
   margin: 1rem;
}

.social-icon{
     margin: 0.25rem;
}
.secend{
    font-size: 30rem;
}
.card {
    display: flex;
    flex-direction: column;
    background-color: aquamarine;
    align-items: center;
    width: 12rem;
    margin: 1rem;
    box-shadow: .25s;
    border-radius: 2px;
}

.image-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 12rem;
    margin: 1rem;
}

.skill-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.row {
    display: flex;
    flex-direction: row;
}
.nav {
        position: fixed;
        /* background-color: rgba(250, 250, 250, 0.04); */
        top: 0;
        width: 100vw;
        height: 3rem;
        flex-direction: row;
        display: flex;
        align-items: center;
}

.nav_menu {
        margin-right: 2rem;
        display: flex;
        flex: 1 1;
        justify-content: flex-end;
        flex-direction: row;
}

.nav_change {
        margin-left: 1rem;
        /* display: flex; */
        /* flex: 1 1; */
        justify-content: flex-start;
}

.fix {
        height: 1vh;
}
.body{
